form {
  position: relative;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #00043b;
  padding: 1.5rem 0;
}

form span {
  position: relative;
  margin: 0 0.3rem;
}

form input {
  padding: 0 1rem;
  height: 38px;
  border: 0;
  background-color: #fff;
}

form input::-webkit-input-placeholder {
  margin: .2rem .5rem;
  text-transform: lowercase;
  font-weight: 700;
  position: absolute;
  top: 0;
  left: 0;
}

form input:-ms-input-placeholder {
  margin: .2rem .5rem;
  text-transform: lowercase;
  font-weight: 700;
  position: absolute;
  top: 0;
  left: 0;
}

form input::-ms-input-placeholder {
  margin: .2rem .5rem;
  text-transform: lowercase;
  font-weight: 700;
  position: absolute;
  top: 0;
  left: 0;
}

form input::placeholder {
  margin: .2rem .5rem;
  text-transform: lowercase;
  font-weight: 700;
  position: absolute;
  top: 0;
  left: 0;
}

form button {
  border: none;
  color: white;
  padding: 0rem 1rem;
  background-color: #bf9300;
  text-transform: uppercase;
  margin-left: .5rem;
  font-weight: 700;
  font-size: 12px;
  letter-spacing: .2px;
}
