section.ctt {
  color: #fff;
  background-color: #000;
}

section.ctt .container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

section.ctt .container .left {
  display: block;
  margin: 0 auto;
  padding: 1.5rem 0;
}

section.ctt .container .left a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-decoration: none;
}

section.ctt .container .left a img {
  margin-right: 1rem;
}

section.ctt .container .left a .data p:first-child {
  font-size: 14px;
  color: #58b137;
}

section.ctt .container .left a .data p:last-child {
  font-size: 18px;
  color: #fff;
}

section.ctt .container .left a .data p:last-child span {
  font-size: 14px;
}

section.ctt .container .right {
  display: block;
  margin: 0 auto;
  padding: 2rem 0;
}

section.ctt .container .right a {
  font-size: 18px;
  text-decoration: none;
  color: white;
  padding: inherit;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: red;
  text-transform: uppercase;
}
