form {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #00043b;
    padding: 1.5rem 0;
    span {
        position: relative;
        margin: 0 0.3rem;
    }

    input {
        padding: 0 1rem;
        height: 38px;
        border: 0;
        background-color: #fff;
        &::placeholder {
            margin: .2rem .5rem;
            text-transform: lowercase;
            font-weight: 700;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    button{
        border: none;
        color: white;
        padding: 0rem 1rem;
        background-color: #bf9300;
        text-transform: uppercase;
        margin-left: .5rem;
        font-weight: 700;
        font-size: 12px;
        letter-spacing: .2px;

    }
}
