footer {
  padding: 2.5rem 0;
  background-color: #d8dade;
}

footer .top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

footer .top .logo {
  width: 50%;
  justify-items: right;
  margin-right: 2.5rem;
}

footer .top .logo img {
  clear: right;
  float: right;
}

footer .top .ctt {
  width: 50%;
}

footer .top .ctt h4 {
  color: #c50717;
}

footer .top .ctt p {
  color: #5b5b5f;
  margin: 1rem 0;
}

footer .bottom {
  padding: 5rem 0 3rem;
}

footer .bottom .adv {
  font-size: 8px;
  float: left;
  clear: both;
}
