section.ctt {
    color: #fff;
    background-color: #000;

    .container {
        display: flex;
        justify-content: space-between;
        .left {
            display: block;
            margin: 0 auto;
            padding: 1.5rem 0;
            a {
                display: flex;
                text-decoration: none;
                img{
                    margin-right: 1rem;
                }
                .data{
                    p:first-child{
                        font-size: 14px;
                        color: #58b137;
                    }
                    p:last-child{
                        font-size: 18px;
                        color: #fff;
                        span{
                            font-size: 14px;
                        }
                    }
                }
            }
        }
        .right {
            display: block;
            margin: 0 auto;
            padding: 2rem 0;
            a {
                font-size: 18px;
                text-decoration: none;
                color: white;
                padding: inherit;
                padding-left: 1rem;
                padding-right: 1rem;
                background-color: red;
                text-transform: uppercase;
            }
        }
    }
}
