.middle {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.5)), url("../img/bgMiddle/bgm1.png");
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    padding-bottom: 8rem;
    .bg {
        img {
            background: url("../img/bgMiddle/bgmOverlay.png");
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
    .middle1 {
        padding: 5rem 0 15rem;
        h2 {
            font-size: 48px;
            color: #bf9300;
            text-align: left;
            margin: 1.5rem 0;
        }
        .slider {
            padding: 2.5rem;
            background-color: #fff;
            img {
                display: block;
                margin: 0 auto;
                min-width: 250px;
                width: 50%;
                height: auto;
            }
        }
    }
    .middle2 {
        h3 {
            width: 70%;
            font-size: 38px;
            color: #bf9300;
            margin-bottom: 3rem;
        }
        p {
            font-size: 18px;
            margin: 0.5rem 0;
            color: #fff;
        }
    }
    .middle3 {
        margin: 5rem 0;
        display: flex;
        justify-content: space-between;
        display: block;
        h3 {
            width: 70%;
            font-size: 38px;
            color: #bf9300;
            margin-bottom: 3rem;
        }
        p {
            font-size: 18px;
            color: #fff;
            margin: 0.5rem 0;
        }
        img {
            float: right;
        }
    }
}
