header {
  background: url("../img/bg.png");
  height: 100vh;
}

header .description {
  z-index: 2;
  position: absolute;
  right: 7%;
  top: 143px;
  text-align: right;
}

header .description h1 {
  color: white;
}

header .description h1 span {
  color: #bf9300;
}

header .propa {
  z-index: 2;
  position: absolute;
  right: 7%;
  top: 243px;
}

header .propa h2 {
  color: white;
}

header .propa h2 .p1 {
  display: block;
  margin: 0;
  padding: 0;
}

header .propa h2 .p2 {
  display: block;
  margin: 0;
  padding: 0;
}

header .propa h2 .p3 {
  display: block;
  margin: 0;
  padding: 0;
}

header .propa h2 .p1 {
  text-align: left;
}

header .propa h2 .p2 {
  color: #bf9300;
  font-size: 52px;
  text-align: center;
  margin: 0.5rem 0;
  border-top: 1px solid #ffffff9a;
  border-bottom: 1px solid #ffffff9a;
}

header .propa h2 .p3 {
  text-align: right;
}

header .bg {
  z-index: 1;
  width: auto;
  height: 100vh;
  position: relative;
  background: url("../img/bgeffect.png");
  background-repeat: no-repeat;
  background-size: 1240px;
  background-position-x: -200px;
  background-position-y: -60px;
}

header .bg .menu {
  z-index: 10;
  position: absolute;
  left: 65px;
  top: 33px;
}

header .bg .menu a {
  background: url("../img/logo.png");
  background-size: contain;
  background-repeat: no-repeat;
  width: 200px;
  height: 200px;
  display: block;
}

header .bg .row {
  z-index: 10;
  position: relative;
  padding-top: 1.5rem;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: end;
}

header .bg .row ul {
  float: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

header .bg .row ul li {
  list-style: none;
}

header .bg .row ul li:nth-child(odd) a {
  background-color: #00043b;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

header .bg .row ul li:nth-child(odd) a:hover {
  background-color: #bf9200ad;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

header .bg .row ul li:nth-child(even) a {
  background-color: #bf9300;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

header .bg .row ul li:nth-child(even) a:hover {
  background-color: #00043bad;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

header .bg .row ul li a {
  display: inline-block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.5px;
  padding: 0.5rem 1.5rem;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

@media screen and (max-width: 975px) {
  header .bg .row {
    padding-top: 0;
  }
  header .bg .row ul {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  header .bg .row ul a {
    width: 100%;
  }
}
