.middle {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.1)), to(rgba(0, 0, 0, 0.5))), url("../img/bgMiddle/bgm1.png");
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.5)), url("../img/bgMiddle/bgm1.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  padding-bottom: 8rem;
}

.middle .bg img {
  background: url("../img/bgMiddle/bgmOverlay.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.middle .middle1 {
  padding: 5rem 0 15rem;
}

.middle .middle1 h2 {
  font-size: 48px;
  color: #bf9300;
  text-align: left;
  margin: 1.5rem 0;
}

.middle .middle1 .slider {
  padding: 2.5rem;
  background-color: #fff;
}

.middle .middle1 .slider img {
  display: block;
  margin: 0 auto;
  min-width: 250px;
  width: 50%;
  height: auto;
}

.middle .middle2 h3 {
  width: 70%;
  font-size: 38px;
  color: #bf9300;
  margin-bottom: 3rem;
}

.middle .middle2 p {
  font-size: 18px;
  margin: 0.5rem 0;
  color: #fff;
}

.middle .middle3 {
  margin: 5rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  display: block;
}

.middle .middle3 h3 {
  width: 70%;
  font-size: 38px;
  color: #bf9300;
  margin-bottom: 3rem;
}

.middle .middle3 p {
  font-size: 18px;
  color: #fff;
  margin: 0.5rem 0;
}

.middle .middle3 img {
  float: right;
}
