@for $i from 1 through 6 {
    .bg#{$i} {
        img {
            display: block;
            position: absolute;
            z-index: 1;
            opacity: 0.5;
        }
    }
}

.bg2 {
    img {
        height: 100vh;
        left: 0;
        top: 0;
    }
}
.bg3 {
    img {
        height: 100vh;
        left: 0;
        top: 0;
    }
}
.bg4 {
    img {
        height: auto;
        right: 0;
        top: 0;
    }
}
.bg5 {
    img {
        height: 100vh;
        left: 0%;
        top: 0;
    }
}
.bg6 {
    img {
        width: 100%;
        height: 100vh;
        left: 0%;
        top: 0;
        z-index: 0;
        z-index: 0.2;
    }
}
.woman {
    img {
        z-index: 0;
        right: 0;
        bottom: 0;
        display: block;
        position: absolute;
        min-width: 700px;
        width: 50%;
        height: auto;
    }
}
.forcasaLogo {
    position: absolute;
    z-index: 2;
    right: 5%;
    bottom: 0;
}
