*{
    font-family: 'Fira Sans', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    .container{
        box-sizing: border-box;
        margin: 0 auto;
        padding: 0;
        .row{
            display: flex;
        }
    }

    @media screen and (min-width: 993px) {
        .container {
            width: 992px;
        }
    }

    @media screen and (min-width: 1240px) {
        .container {
            width: 1239px;
        }
    }
    @media screen and (min-width: 1440px) {
        .container {
            width: 1239px;
        }
    }
}
