footer {
    padding: 2.5rem 0;
    background-color: #d8dade;
    .top {
        display: flex;
        justify-content: space-between;
        .logo {
            width: 50%;
            justify-items: right;
            margin-right: 2.5rem;
            img {
                clear: right;
                float: right;
            }
        }
        .ctt {
            width: 50%;
            h4 {
                color: #c50717;
            }
            p {
                color: #5b5b5f;
                margin: 1rem 0;
            }
        }
    }
    .bottom {
        padding: 5rem 0 3rem;
        .adv {
            font-size: 8px;
            float: left;
            clear: both;
        }
    }
}
