header {
    background: url("../img/bg.png");
    height: 100vh;
    .description {
        z-index: 2;
        position: absolute;
        right: 7%;
        top: 143px;
        text-align: right;
        h1 {
            color: white;
            span {
                color: #bf9300;
            }
        }
    }
    .propa {
        z-index: 2;
        position: absolute;
        right: 7%;
        top: 243px;
        h2 {
            color: white;
            @for $i from 1 through 3 {
                .p#{$i} {
                    display: block;
                    margin: 0;
                    padding: 0;
                }
            }
            .p1 {
                text-align: left;
            }
            .p2 {
                color: #bf9300;
                font-size: 52px;
                text-align: center;
                margin: 0.5rem 0;
                border-top: 1px solid #ffffff9a;
                border-bottom: 1px solid #ffffff9a;
            }
            .p3 {
                text-align: right;
            }
        }
    }
    .bg {
        z-index: 1;
        width: auto;
        height: 100vh;
        position: relative;
        background: url("../img/bgeffect.png");
        background-repeat: no-repeat;
        background-size: 1240px;
        background-position-x: -200px;
        background-position-y: -60px;
        .menu {
            z-index: 10;
            position: absolute;
            left: 65px;
            top: 33px;
            a {
                background: url("../img/logo.png");
                background-size: contain;
                background-repeat: no-repeat;
                width: 200px;
                height: 200px;
                display: block;
            }
        }
        .row {
            z-index: 10;
            position: relative;
            padding-top: 1.5rem;
            justify-content: end;
            ul {
                float: right;
                display: flex;
                li {
                    list-style: none;
                    &:nth-child(odd) a {
                        background-color: #00043b;
                        transition: all 0.2s linear;
                        &:hover {
                            background-color: #bf9200ad;
                            transition: all 0.2s linear;
                        }
                    }
                    &:nth-child(even) a {
                        background-color: #bf9300;
                        transition: all 0.2s linear;
                        &:hover {
                            background-color: #00043bad;
                            transition: all 0.2s linear;
                        }
                    }
                    a {
                        display: inline-block;
                        color: white;
                        text-decoration: none;
                        text-transform: uppercase;
                        font-size: 12px;
                        font-weight: 700;
                        letter-spacing: 0.5px;
                        padding: 0.5rem 1.5rem;
                        transition: all 0.2s linear;
                    }
                }
            }
            @media screen and (max-width: 975px) {
                padding-top: 0;
                ul {
                    flex-direction: column;
                    a {
                        width: 100%;
                    }
                }
            }
        }
    }
}
